import "babel-polyfill";
import Vue from "vue";
import App from "./App.vue";
import "@/assets/css/index.less";
import router from "./router/index";
import store from "./store/index";
import Antd from "ant-design-vue";
import "ant-design-vue/dist/antd.less";
import validateRules from "@/utils/validateRules";
import api from "@/api/api";
import 'vant/lib/index.css'
import Vant from 'vant'
import TIM from 'tim-js-sdk';
import COS from "cos-js-sdk-v5";
import genTestUserSig from './GenerateTestUserSig'
import echarts from 'echarts'

let options = {
  SDKAppID: 1400449484// 0 IM  SDKAppID
};
//  SDK `TIM.create()` `SDKAppID` 
let tim = TIM.create(options); // SDK  tim 
//  SDK  setLogLevel 
tim.setLogLevel(0); // 
// tim.setLogLevel(1); // release SDK 
//  COS SDK 
tim.registerPlugin({'cos-js-sdk': COS});

Vue.use(Antd);
Vue.use(Vant);

Vue.config.productionTip = false;
Vue.prototype.$bus = new Vue();
Vue.prototype.validateRules = validateRules;
Vue.prototype.api = api;
window.tim = tim
window.TIM = TIM
Vue.prototype.tim = tim
Vue.prototype.TIM = TIM
Vue.prototype.$genTestUserSig = genTestUserSig;
Vue.prototype.$echarts = echarts

let _this = new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");

export default _this;
